
body {
  background-color: #fafafa;
}
#app {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1000px;
  height: 600px;
  margin-top: -300px;
  margin-left: -500px;
  border-radius: 3px;
  background-color: #444;
  -webkit-box-shadow: 0px 2px 2px 2px rgba(0, 0, 0, 0.3);
          box-shadow: 0px 2px 2px 2px rgba(0, 0, 0, 0.3);
}
#app:after {
  content: '';
  display: block;
  clear: both;
}
#app > #infomation {
  float: left;
  width: 600px;
}
#app > #infomation > #current-weather {
  margin-top: 70px;
  height: 180px;
}
hr {
  width: 80%;
  margin: 80px auto;
  border: none;
  border-top: 1px solid white;
}
#app > #infomation > #forecast {
  height: 400px;
}
#app > #map {
  float: right;
  width: 400px;
  height: 600px;
}



